import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Chip from '@mui/material/Chip';
import LanguageIcon from '@mui/icons-material/Language';
import ShareIcon from '@mui/icons-material/Share';
import data from './assets/data'

interface Data {
  logo: string;
  link: string;
  code: string;
  type: string;
}

function App() {
  const handleLinkClick = ({ code } : Data) => {
    navigator.clipboard.writeText(code);
		window.alert("");
  }
  const handleShareClick = ({ link } : Data) => {
    navigator.clipboard.writeText(link);
		window.alert("");
  }
  const handleBrowserClick = ({ link } : Data) => {
    window.open(link)
  }

  return (
    <Container maxWidth="sm" sx={{ padding: '10 5' }}>
      <Typography variant="h4" component="h4" gutterBottom>
        Welcome! Looking for some referral code?
      </Typography>
      <Typography paragraph component="p">
        Here have what you want!
      </Typography>

      {data.map((item, index) =>
        <Card sx={{ display: 'flex', marginBottom: 4 }} key={index}>
          <Box sx={{ width: '100%' }}>
            <CardContent sx={{ display: 'inline-flex', flexDirection: 'column', flex: '1 0 auto' }}>
              <Typography variant="h6" component="h6" gutterBottom>{item.brand}</Typography>
              {item.code && <Chip label={item.code} variant="outlined" onClick={() => handleLinkClick(item)} />}
              <Box sx={{ marginTop: 2 }}>
                <LanguageIcon onClick={() => handleBrowserClick(item)} />
                <ShareIcon sx={{ marginLeft: 1 }} onClick={() => handleShareClick(item)} />
              </Box>
            </CardContent>
          </Box>
          <CardMedia
            component="img"
            sx={{ width: 156, height: 156 }}
            image={item.logo}
            alt={item.brand}
          />
        </Card>
      )}
    </Container>
  );
}

export default App;
